@media (min-width: 992px) and (max-width: 1199px){
    body{
        border: 24px double orange;
        padding: 45px 0;
    }
    .boardMatch{
        width:500px;
        height:500px;
        margin-top: 22px;
    }

    #cubes{
        width: 79.1px;
        height: 79.1px;
    }
    #emoji{
        width: 69.1px;
        height: 69.1px;
        margin-right: 60px;
    }
    .a button{
        width: 130px;
        height:45px;
        border-radius: 8px;
    }
    .a h2{
        font-size: 32px;
    }
    .winFrame{
        width: 430px;
        height: 230px;
        top: 225px;
        border-radius: 18px;
        margin-left: 85px;
    }
    .winFrame h3{
        font-size: 25px;
    }
    .winFrame button{
        width: 150px;
        height: 45px;
        border-radius: 8px;
        font-size: 15px;
    }


}
