.mainBoard{
    width: 100%;
    height: 110vh;
    display: flex;
    align-items: center;
    justify-content: space-around;
    flex-direction: column;

}
body{
    border: 26px double orange;
    margin: 0;
    padding: 50px 0;
    box-sizing: border-box;
}
.boardMatch{
    width:600px;
    height:600px;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    justify-content: space-between;
    box-shadow: 0px 1px 8px 1px #000000;
    position: relative;
    margin-top: 25px;
}

#cubes{
    width: 95px;
    height: 95px;
    display: flex;
    align-items: center;
    justify-content: center;
}
#cubes img{
    width: 90%;
    height: 90%;
    cursor: pointer;
}
#emoji{
    width: 80px;
    height: 80px;
    margin-right: 70px;
}
.a{
    width: 45%;
    height: fit-content;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.a button{
    width: 150px;
    height: 50px;
    background: orange;
    color: white;
    border-radius: 10px;
    border: 0;
}
.a button:hover{
    background: crimson;
    transition: 1s;
    cursor: pointer;
}
.a h2{
    color: orange;
    font-size: 40px;
    font-family: 'Orbitron', sans-serif;
}

.winFrame{
    width: 450px;
    height: 250px;
    background: rgb(237,103,19);
    background: linear-gradient(90deg, rgba(237,103,19,1) 21%, rgba(238,12,47,1) 88%);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 250px;
    border-radius: 20px;
    margin-left: 100px;
    z-index: 999;
}
.winFrame h3{
    color: white;
    font-size: 30px;
}
.winFrame button{
    width: 170px;
    height: 50px;
    border: 0;
    border-radius: 10px;
    color: orange;
    font-weight: bold;
    font-size: 18px;
    cursor: pointer;
}

